import avatar from "@/components/yq-avatar/yq-avatar.vue";
export default {
	components: {
		avatar
	},
	data() {
		return {
			userImg:'',
			avatarShow: false,
			sexList:[{
				id:0,
				title:'男'
			},{
				id:1,
				title:'女'
			},],
			sexIndex:0
		};
	},
	methods:{
		openavatar(){
			this.avatarShow = true
			this.$refs.avatar.fChooseImg(1, {
				selWidth: "300upx",
				selHeight: "300upx",
				expWidth: '260upx',
				expHeight: '260upx'
			});
		},
		myUpload(rsp) {
			this.avatarShow = false
			this.userImg = rsp.base64
		},
		bindSexChange(e){
			this.sexIndex = e.detail.value
		},
		gotoNickname(){
			wx.navigateTo({
				url:'/pages/mine/editNickname'
			})
		}
	}
}
